body {
    font-family: 'Roboto', sans-serif !important;
    background-color: #f4f5fa !important;
  }
  
  .login-container {
    margin-top: 10%;
    border: 1px solid #CCD1D1;
    border-radius: 5px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    max-width: 60% !important;
    background-color: #fff;
  }
  
  .ads {
    background-color: #1672ec;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
    color: #fff;
    padding: 15px;
    text-align: center;
  }
  
  .ads h1 {
    margin-top: 20%;
  }
  
  #fl {
    font-weight: 600;
  }
  
  #sl {
    font-weight: 100 !important;
  }
  
  .profile-img {
    text-align: center;
  }
  
  .profile-img img {
    border-radius: 50%;
    /* animation: mymove 2s infinite; */
  }
  
  @keyframes mymove {
    from {border: 1px solid #F2F3F4;}
    to {border: 8px solid #F2F3F4;}
  }
  
  .login-form {
    padding: 15px;
  }
  
  .login-form h3 {
    text-align: center;
    padding-top: 15px;
    padding-bottom: 15px;
  }
  
  .form-control {
    font-size: 14px;
  }
  
  .forget-password a {
    /* font-weight: 500; */
    text-decoration: none;
    font-size: 14px;
  }
  img.logo {
    width: 100%;
    height: auto;
  }

  img.logo-icon {
    width: 18%;
    height: auto;
  }
  .form-group {
    margin-bottom: 20px;
  }
  .login-button {
    background-color: #1672ec !important;
    border-color: #1672ec !important;
    width: 100%;
  }
  .forget-password.form-group {
      text-align: left;
}